@import '../styles/reset.scss';
@import '../styles/variables.scss';

.desserts-container {
    min-height: 100vh;
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding: 1.6875rem;
    background-color: $rose-100;

    @media screen and (min-width: 700px) {
        flex-direction: row;
        flex-wrap: wrap;
    }

    .desserts-list {
        display: flex;
        flex-direction: column;

        @media screen and (min-width: 700px) {
            max-width: 50rem;
            flex-direction: row;
            flex-wrap: wrap;
            gap: 1.45rem;
        }

        .dessert-item {
            display: flex;
            flex-direction: column;

            @media screen and (min-width: 700px) {
                width: 250px;
            }

            .dessert-image-container {
                display: flex;
                flex-direction: column;
                align-items: center;
                position: relative;

                .dessert-image img {
                    width: 100%;
                    height: auto;
                    border-radius: 0.6rem;
                }
            }
        }
    }

    .dessert-details {
        display: flex;
        flex-direction: column;
        align-items: start;
        margin-top: 1.5rem;

        .dessert-category {
            font-weight: 400;
            font-size: 0.9rem;
            color: $rose-500;
        }

        .dessert-name {
            margin: 0;
            font-weight: 600;
            color: $rose-900;
        }

        .dessert-price {
            margin: 0 0 1rem 0;
            font-weight: 600;
            color: $red;
        }
    }
}
